  display: inline-block;
  font-size: 1.6rem;
  font-weight: 400;
}
.write-rate .header .good[data-v-e0387c38] {
  background-color: #fbe69a;
  border-radius: 4px;
  color: #ce8000;
  display: inline-block;
  font-size: 1.1rem;
  line-height: 14px;
  padding: 3px;
}
.write-rate fieldset[data-v-e0387c38] {
  border: 0;
  border-top: 1px solid #E4E4E4;
}
.write-rate fieldset legend[data-v-e0387c38] {
  margin: 0 auto;
  padding: 0 2rem;
  color: #999;
}
.write-rate .desc[data-v-e0387c38] {
  font-size: 1.4rem;
  color: #999;
  text-align: center;
  margin-bottom: 1.5rem;
}
.write-rate .stars[data-v-e0387c38] {
  margin-top: 2.5rem;
  margin-bottom: 1.5rem;
  line-height: 1;
  text-align: center;
}
.write-rate .stars .iconfont[data-v-e0387c38] {
  color: #ddd;
  font-size: 3.0rem;
  margin: 0 4px;
}
.write-rate .stars .on[data-v-e0387c38] {
  color: #fdd700;
}
.write-rate .tags[data-v-e0387c38]:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.write-rate .tags span[data-v-e0387c38] {
  background-color: #f0f0f0;
  border-radius: 3px;
  display: block;
  float: left;
