  text-align: center;
  width: 32%;
}
.write-rate .tags span[data-v-e0387c38]:nth-child(3),
.write-rate .tags span[data-v-e0387c38]:nth-child(6) {
  margin-right: 0;
}
.write-rate .tags span.on[data-v-e0387c38] {
