  padding: 1rem;
  width: 100%;
}
.write-rate .switch[data-v-e0387c38] {
  color: #999;
  font-size: 1.4rem;
  line-height: 50px;
}
.write-rate .submit[data-v-e0387c38] {
  bottom: 0;
  left: 0;
  padding: 1.5rem;
  position: absolute;
  width: 100%;
}
