.spread {
  padding: 1.5rem ;
}
.spread .generate {
  background-color: #f0f0f0 ;
  border-radius: 3px;
  color: #4081d6;
  display: block ;
  font-size: 1.4rem ;
  height: 45px ;
  line-height: 45px ;
  text-align: center ;
}



