.multi-rates dt {
  color: #999;
  font-size: 1.4rem ;
  line-height: 50px ;
}
.multi-rates dt .iconfont {
  color: #fdd700;
  font-size: 1.2rem ;
}
.multi-rates dt .number {
  color: #4e4e4e;
  font-size: 2.0rem ;
}
.multi-rates .tags:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.multi-rates .tags span {
  background-color: #e1f0ff ;
  border-radius: 4px;
  display: block ;
  float: left ;
  font-size: 1.4rem ;
  line-height: 30px ;
  margin-right: 2% ;
  margin-bottom: 8px ;
  text-align: center ;
  width: 32% ;
}
.multi-rates .tags span:nth-child(3),
.multi-rates .tags span:nth-child(6) {
  margin-right: 0 ;
}
.multi-rates .tags span.bad {
  background-color: #f5f5f5 ;
  color: #999;
}



