.rate {
  border-top: 1px solid #f9f9f9;
  padding: .5rem 0 ;
}
.rate dt .iconfont {
  color: #fdd700;
  font-size: 1rem ;
}
.rate dd {
  line-height: 22px ;
}
.rate .content {
  font-size: 1.4rem ;
}
.rate .summary {
  color: #999;
  font-size: 1.2rem ;
}



