.login-canvas[data-v-8e94db9e] {
  background-image: url(../../assets/login-bg.jpg);
  background-size: 100% 100% ;
  height: 100% ;
  padding-top: 6rem ;
  position: relative ;
  width: 100% ;
}
.login-canvas dl[data-v-8e94db9e] {
  background-color: #fff ;
  border-radius: 5px;
  margin: 0 auto ;
  padding: 1rem 2.5rem 1.5rem 2.5rem ;
  width: 90% ;
}
.login-canvas dl dt[data-v-8e94db9e] {
  font-size: 2.1rem ;
  font-weight: 400 ;
  line-height: 6rem ;
  text-align: center ;
}
.login-canvas dl button[data-v-8e94db9e] {
  margin: 2.5rem 0 1.5rem 0 ;
}
.login-canvas dl .item[data-v-8e94db9e] {
  border-bottom: 1px solid #E4E4E4;
  padding: .5rem 0 ;
}
.login-canvas dl .item[data-v-8e94db9e]:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.login-canvas dl .item input[type='text'][data-v-8e94db9e] {
  font-size: 1.6rem ;
  line-height: 30px ;
  width: 100% ;
}
.login-canvas dl .item .verify-code-input[data-v-8e94db9e] {
  margin-right: 65px ;
}
.login-canvas dl .item .verify-code-pict[data-v-8e94db9e] {
  float: right ;
  width: 65px ;
}
.login-canvas dl .item .verify-code-pict img[data-v-8e94db9e] {
  height: 28px ;
  width: 65px ;
}
.login-canvas dl .item .verify-code-pict .count-down[data-v-8e94db9e],
.login-canvas dl .item .verify-code-pict a[data-v-8e94db9e] {
  line-height: 28px ;
}
.login-canvas dl .item .verify-code-pict a[data-v-8e94db9e] {
  color: #4081d6;
}
.login-canvas dl .audio-verify[data-v-8e94db9e] {
  text-align: center ;
}
.login-canvas dl .audio-verify a[data-v-8e94db9e] {
  color: #4081d6;
}
.login-canvas .tips[data-v-8e94db9e] {
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 5px;
  color: #fff ;
  font-size: 1.2rem ;
  line-height: 18px ;
  margin: 3rem auto ;
  padding: .8rem 0 ;
  text-align: center ;
  width: 80% ;
}
.login-canvas .footer[data-v-8e94db9e] {
  bottom: 0 ;
  color: #fff ;
  font-size: 1.2rem ;
  left: 0 ;
  line-height: 40px ;
  position: absolute ;
  text-align: center ;
  width: 100% ;
}
.login-canvas .footer a[data-v-8e94db9e] {
  color: #fff ;
}



