.assistant {
  background-color: #fff ;
  box-shadow: 0 -2px 6px 0 rgba(0, 0, 0, 0.1);
  bottom: 0 ;
  height: 50px ;
  left: 0 ;
  position: fixed ;
  width: 100% ;
  z-index: 9900 ;
}
.assistant:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.assistant > li {
  float: left ;
  height: 50px ;
  text-align: center ;
  width: 33.3% ;
}
.assistant > li:last-child {
  border: none ;
}
.assistant .portrait {
  background-color: white ;
}
.assistant .portrait a {
  color: #4e4e4e;
  display: block ;
}
.assistant .portrait dl {
  display: inline-block;
  padding: 4px 0 ;
}
.assistant .portrait dl:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.assistant .portrait dl dt {
  border-radius: 100px;
  float: left ;
  height: 34px ;
  margin: 4px 0 ;
  overflow: hidden ;
  width: 34px ;
}
.assistant .portrait dl dt img {
  width: 100%;
}
.assistant .portrait dl dd {
  line-height: 21px ;
  margin-left: 40px ;
  text-align: left ;
}
.assistant .portrait dl dd.agent-name {
  font-size: 1.4rem ;
}
.assistant .portrait dl dd.agent-tel {
  color: #999;
  font-size: 1.1rem ;
}
.assistant .tools a {
  color: #4081d6;
  display: block ;
  font-size: 1.4rem;
  margin-top: 0;
  line-height: 50px;
}
.assistant .tools a span {
  display: block ;
  text-align: center ;
  line-height: 50px;
}
.assistant .phone-consult {
  background-color: #4081d6;
}
.assistant .phone-consult a {
  color: white;
}
.assistant .wx {
  background-color: #FC4C5A;
  color: #fff;
}
.assistant .wx a {
  color: #fff;
}
.assistant.no-weixin .wx {
  display: none;
}
.assistant.no-weixin .phone-consult {
  width: 66.6% ;
}
.assistant.no-weixin .phone-consult a {
  line-height: 34px ;
}
.assistant.no-weixin .phone-consult a span {
  display: inline-block;
}
.assistant .bubble {
  position: absolute;
  display: none;
  color: #fff;
  top: -54px;
  left: 1rem;
}
.assistant .bubble.hidden {
  display: none !important;
}
.assistant .bubble .content {
  width: 14rem;
  height: 4.1875rem;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 1rem;
  font-size: 1.4rem;
  padding: 1.1rem 0rem;
  line-height: 1.98rem;
  box-sizing: border-box;
  text-align: center;
}
.assistant .bubble .triangle {
  width: 0;
  height: 0;
  border-left: 1rem solid transparent;
  border-right: 1rem solid transparent;
  border-top: 0.78rem solid rgba(0, 0, 0, 0.7);
  position: relative;
  left: 1.6rem;
}
.wkzf-modal {
  width: 27rem;
  max-width: 80%;
  border-radius: 12px;
}
.wkzf-modal .wkzf-btn:nth-child(2) {
  color: #FC4C5A;
  font-size: 1.6rem;
  border-left: none;
}
.wkzf-tips {
  z-index: 9999;
}



