/*//form表单元素*/
.article {
  background-color: #f9f9f9;
  padding-bottom: 50px ;
}
.article .article-title {
  font-size: 2.4rem;
  margin-left: 1.5rem;
  padding-top: 1.5rem;
  line-height: 1.4;
  color: #4e4e4e;
  font-weight: normal;
  background-color: white;
}
.article .article-description {
  margin-top: .5rem;
  margin-left: 1.5rem;
  margin-bottom: 2rem;
  font-size: 1.4rem;
  color: #999;
  font-weight: normal;
  background-color: white;
}
.article .article-description .date {
  margin-right: 1.5rem;
  float: right;
}
.article .article-description .visit-number {
  float: right;
  margin-right: 2rem;
}
.article .article-description .visit-number .num {
  color: #4e4e4e;
}
.article .article-content {
  background-color: white;
  padding: 0 1.5rem 1.0rem 1.5rem;
}
.article .article-content img {
  width: 100%;
}
.article .article-statistics {
  padding: 0 1.5rem 2.4rem 1.5rem;
  color: #3f3f3f;
  font-size: 1.4rem;
}
.article .article-statistics .num {
  color: #000;
}
.article .article-comments {
  margin-top: 1rem;
}
.article .article-comments h1 {
  font-weight: 600;
  margin-left: 1.5rem;
  border: none;
}
.article .article-comments .operate {
  margin-top: 1.9rem;
  font-size: 1.4rem;
  color: #7C7C7C;
  text-align: right;
}
.article .article-comments .operate .btn {
  border: none;
  background-color: white;
  color: #999;
}
.article .article-comments .operate .btn.confirm {
  padding: .4rem 1.2rem;
  border: 1px solid #4081D6;
  color: #4081D6;
  /*#7C7C7C;*/
  border-radius: .2rem;
  margin-left: 3rem;
  background-color: white;
}
.article .article-comments .operate .btn.confirm[disabled] {
  border: 1px solid #999;
  color: #999;
}
.comments {
  margin-top: 3.4rem;
}
.iconfont.active {
  color: #92A7C3 ;
}
.float-call {
  position: fixed;
  bottom: 8rem;
  right: 1.5rem;
  background-color: white;
  text-align: center;
  box-shadow: 0 2px 3px 0;
  border-radius: 3px;
  font-size: 1rem;
  line-height: 1.4;
  width: 5rem;
  padding-top: 0.8rem;
  padding-bottom: 0.2rem;
}
.float-call:active,
.float-call:focus,
.float-call:link,
.float-call:visited {
  color: #4e4e4e;
}
.float-call * {
  display: block;
}
input {
  border: 1px solid #ccc;
}
#app .article .assistant .bubble {
  display: block;
}



