#esfDetailShare {
  /*
		这个页面的内边距跟之前预设的不一样
	*/
  padding-bottom: 50px;
}
#esfDetailShare .swiper-container {
  min-height: 210px;
  background-color: black;
}
#esfDetailShare .wk-panel {
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  margin-bottom: 1rem;
}
#esfDetailShare .wk-panel .panel-header {
  margin-left: 0;
  font-weight: 600;
}
#esfDetailShare .wk-panel .panel-body {
  padding-left: 0;
  padding-right: 0;
  padding-bottom: .5rem;
}
#esfDetailShare .swiper-slide {
  height: auto;
  position: relative;
  overflow: hidden;
}
#esfDetailShare .swiper-slide .pagination {
  position: absolute;
  right: 1.4rem;
  bottom: 1.4rem;
  font-size: 1.2rem;
  line-height: 2.3rem;
  border-radius: 1.2rem;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  width: 4.7rem;
  text-align: center;
}
#esfDetailShare .summary h1 {
  font-size: 2rem;
  line-height: 1.4;
  color: #4e4e4e;
  font-weight: 600;
  padding-bottom: .8rem;
  margin-top: 1rem;
}
#esfDetailShare .summary .basic {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 1.5rem;
}
#esfDetailShare .summary .basic li + li {
  border-left: 1px solid #E4E4E4;
  padding-left: 2rem;
}
#esfDetailShare .summary .basic dt {
  font-size: 1.2rem;
  color: #7C7C7C;
  margin-bottom: .5rem;
}
#esfDetailShare .summary .basic dd {
  font-size: 1.4rem;
  color: #FC4C5A;
  font-weight: 600;
}
#esfDetailShare .summary .basic li:nth-child(1),
#esfDetailShare .summary .basic li:nth-child(3) {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#esfDetailShare .summary .basic li:nth-child(2) {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
}
#esfDetailShare .summary .outline .tags {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}
#esfDetailShare .summary .outline .tags > span {
  font-size: 1.2rem;
  line-height: 2.3rem;
  color: #4e4e4e;
  margin-right: .6rem;
  background-color: #F0F0F0;
  padding: .4rem .5rem;
  border-radius: 0.3rem;
}
#esfDetailShare .summary .outline .tags > span.jx {
  color: #FFFFFF;
  background: #4081D6;
}
#esfDetailShare .summary .outline .tags > span.mw {
  color: #4081D6;
  background: #CEE3FF;
}
#esfDetailShare .summary .outline .tags > span.dt {
  color: #D28310;
  background: #FFE695;
}
#esfDetailShare .summary .outline .tags > span.jxx {
  color: #DA792B;
  background: #FFE4CE;
}
#esfDetailShare .summary .outline .tags > span.xs {
  color: #ED7474;
  background: #FFE0E0;
}
#esfDetailShare .summary .outline .tags > span.tt {
  color: #3F8D47;
  background: #C4E9C7;
}
#esfDetailShare .summary .outline .tags > span.xj {
  color: #7c7c7c;
  background: #f5f5f5;
}
#esfDetailShare .summary .outline dl {
  margin-bottom: 1.5rem;
}
#esfDetailShare .summary .outline dt {
  font-size: 1.4rem;
  color: #7C7C7C;
  display: block;
  margin-right: 1.5rem;
  float: left;
}
#esfDetailShare .summary .outline dd {
  font-size: 1.4rem;
  display: block;
  /*
    *截取文本用省略号追加在后面
    */
  overflow: hidden ;
  white-space: nowrap ;
  text-overflow: ellipsis ;
  color: #000;
}
#esfDetailShare .summary .outline .left {
  width: 60%;
  float: left;
}
#esfDetailShare .summary .outline .right {
  width: 40%;
  float: left;
}
#esfDetailShare .summary .outline .full::before {
  content: "";
  display: table;
  clear: both;
}
#esfDetailShare .summary .estate-info dl {
  margin-bottom: 1.5rem;
}
#esfDetailShare .summary .estate-info dt {
  font-size: 1.4rem;
  color: #7C7C7C;
  display: block;
  margin-right: 1.5rem;
  float: left;
}
#esfDetailShare .summary .estate-info dd {
  font-size: 1.4rem;
  display: block;
  /*
    *截取文本用省略号追加在后面
    */
  overflow: hidden ;
  white-space: nowrap ;
  text-overflow: ellipsis ;
  margin-left: 4rem;
  color: #000;
}
#esfDetailShare .summary .estate-info dd a {
  display: block;
  /*
    *截取文本用省略号追加在后面
    */
  overflow: hidden ;
  white-space: nowrap ;
  text-overflow: ellipsis ;
  width: 100%;
  color: #4081D6;
  position: relative;
  padding-right: 1rem;
}
#esfDetailShare .summary .estate-info dd .icon-arrowR {
  font-size: 1.4rem;
  right: 0;
  top: 2px;
  position: absolute;
}
#esfDetailShare .summary .estate-info .full dd {
  overflow: inherit;
  white-space: inherit;
}
#esfDetailShare .summary .estate-info .full::before {
  content: "";
  display: table;
  clear: both;
}
#esfDetailShare .basic-info {
  padding-bottom: 1rem;
}
#esfDetailShare .basic-info .panel-header {
  border: none;
  padding-bottom: 0;
  padding-top: 1.5rem;
}
#esfDetailShare .basic-info .panel-body {
  padding-top: 0;
}
#esfDetailShare .basic-info dl {
  font-size: 1.4rem;
}
#esfDetailShare .basic-info dl dt {
  color: #7C7C7C;
  margin-bottom: .5rem;
  margin-top: 1.5rem;
}
#esfDetailShare .basic-info dl dd {
  color: #000;
}
#esfDetailShare .basic-info .more {
  display: block;
  color: #4081D6;
  margin-top: 1.5rem;
}
#esfDetailShare .estate-info {
  margin-bottom: 0;
}
#esfDetailShare .estate-info .panel-header {
  border: none;
  padding-bottom: 0;
  padding-top: 1.5rem;
}
#esfDetailShare .estate-info img {
  width: 12rem;
  height: auto;
  height: 8rem;
  float: left;
  display: inline-block;
}
#esfDetailShare .estate-info .estate {
  margin-left: 13rem;
  display: block;
  font-size: 1.4rem;
  position: relative;
}
#esfDetailShare .estate-info .estate .icon-arrowR {
  font-size: 1.4rem;
  line-height: 1;
  position: absolute;
  right: 0;
  top: 50%;
  color: #4081D6;
  margin-top: -0.7rem;
}
#esfDetailShare .estate-info .estate p {
  color: #000;
  line-height: 1.43;
}
#esfDetailShare .estate-info .estate p + p {
  margin-top: 1rem;
  color: #7C7C7C;
}
#esfDetailShare .estate-info .estate::after {
  content: "";
  display: table;
  clear: both;
}
#esfDetailShare .estate-info h4 {
  margin: 1.5rem 0;
  font-size: 1.4rem;
}
#esfDetailShare .estate-info .list-info {
  font-size: 1.4rem;
}
#esfDetailShare .estate-info .list-info .icon-arrowR {
  font-size: 1.4rem;
}
#esfDetailShare .estate-info .list-info li {
  padding: 1.5rem 0;
}
#esfDetailShare .estate-info .list-info li a {
  display: block;
}
#esfDetailShare .estate-info .list-info li a:active,
#esfDetailShare .estate-info .list-info li a:link,
#esfDetailShare .estate-info .list-info li a:visited,
#esfDetailShare .estate-info .list-info li a:visited {
  color: #000;
}
#esfDetailShare .estate-info .list-info li + li {
  border-top: 1px solid #E4E4E4;
}
#esfDetailShare .estate-info .list-info .count {
  float: right;
  color: #4081D6;
}
#esfDetailShare .location {
  display: block;
  position: relative;
  height: 0;
  overflow: hidden;
  width: 100%;
  padding-bottom: 66.5625%;
  background-size: cover;
}
#esfDetailShare .location img {
  width: 100%;
}
#esfDetailShare .location .map-info {
  border-radius: 50%;
  background-color: #fff;
  width: 2rem;
  height: 2rem;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -1rem;
  margin-top: -1rem;
}
#esfDetailShare .location .map-info .inner {
  background-color: #4081D6;
  vertical-align: middle;
  width: 1.5rem;
  height: 1.5rem;
  display: inline-block;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -0.75rem;
  margin-top: -0.75rem;
}
#esfDetailShare .location .container {
  position: absolute;
  top: 50%;
  width: 100%;
  margin-top: 2rem;
}
#esfDetailShare .location .map-bubble {
  margin: 0 auto;
  top: 50%;
  font-size: 1.4rem;
  background-color: #fff;
  max-width: 80%;
  width: 50%;
  position: relative;
  border-radius: 4px;
}
#esfDetailShare .location .map-bubble .triangle {
  width: 0;
  height: 0;
  border-left: .5rem solid transparent;
  border-right: .5rem solid transparent;
  border-bottom: .9rem solid #fff;
  position: absolute;
  top: -0.9rem;
  left: 50%;
  margin-left: -0.5rem;
}
#esfDetailShare .location .map-bubble .box {
  padding: 1.6rem 0;
  text-align: center;
  color: black;
}
#esfDetailShare #esfDetailShare {
  padding-bottom: 60px;
}
#esfDetailShare .more {
  font-size: 1.6rem;
  color: #4081D6;
}
#esfDetailShare .similar-esf {
  padding-bottom: 1.5rem;
}
#esfDetailShare .similar-esf .panel-header {
  font-size: 1.6rem;
  border: none;
  line-height: 2rem;
}
#esfDetailShare #cover {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 9901;
  background-color: #fff;
}
#esfDetailShare #cover .swiper-container {
  width: 100%;
  height: 100%;
}
#esfDetailShare #cover .swiper-slide {
  width: 100%;
  height: 100%;
}
#esfDetailShare #cover .img-container {
  width: 100%;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
}
#esfDetailShare #cover .pagination {
  top: 1.4rem;
  bottom: auto;
}
#esfDetailShare .ext-house-desc {
  margin-top: 1.5rem;
  line-height: 1.428;
  font-size: 1.4rem;
}
#esfDetailShare .ext-house-desc span {
  vertical-align: top;
  color: #4081D6;
}
.swiper-wrapper img {
  width: 100%;
  height: 210px;
}



