/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 1. 项目名称：yfyk2h5
 2. 文件名称：src -> less -> rem.less (适配屏幕宽度的1rem=?pix的动态设置)
 3. 作者：zhaohuagang@lifang.com
 4. 备注：
    以Iphone6位标准(375px)屏幕为 20px，以此为基础计算出每一种宽度的字体大小
    375以下不变，375以上等比放大
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
html {
  font-family: microsoft yahei, simsun;
  font-size: 10px;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
页面背景色及字体颜色设置
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
body {
  background: #fbf9fe;
  color: #4e4e4e;
  font-family: microsoft yahei, simsun;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
循环设置各种尺寸下的html节点的字体大小，以实现适配，注意还要是竖向的orientation : portrait，横向是：landscape
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
@media only screen and (min-width: 400px) and (orientation: portrait) {
html {
    font-size: 10.66666667px !important;
    font-family: microsoft yahei, simsun;
}
}
@media only screen and (min-width: 414px) and (orientation: portrait) {
html {
    font-size: 11.04px !important;
    font-family: microsoft yahei, simsun;
}
}
@media only screen and (min-width: 480px) and (orientation: portrait) {
html {
    font-size: 12.8px !important;
    font-family: microsoft yahei, simsun;
}
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 1. 项目名称：yfyk2h5
 2. 文件名称：src -> less -> reset.less (html标签重定义)
 3. 作者：zhaohuagang@lifang.com
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
* {
  margin: 0 ;
  padding: 0 ;
  vertical-align: middle ;
  box-sizing: border-box;
}
li {
  list-style: none ;
}
a {
  outline: none ;
}
a:link {
  text-decoration: none ;
}
a:visited {
  text-decoration: none ;
}
a:active {
  text-decoration: none ;
}
a:focus {
  text-decoration: none ;
}
textarea {
  resize: none ;
}
hr {
  border: none ;
  border-top: 1px solid #E4E4E4;
  height: 0 ;
  margin: 1rem 0 ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 1. 项目名称：yfyk2h5
 2. 文件名称：src -> less -> normalize.less (整个应用的常用的class)
 3. 作者：zhaohuagang@lifang.com
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 通体透明
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.transparent {
  background-color: transparent ;
  border: 1px solid transparent ;
  outline: none ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 禁用全角输入法
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.ime-disabled {
  ime-mode: disabled ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 上下左右边界定义
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.top-gap {
  margin-top: 1rem;
}
.right-gap {
  margin-right: 1rem;
}
.bottom-gap {
  margin-bottom: 1rem;
}
.left-gap {
  margin-left: 1rem;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
上下和左右padding设置
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.lr-padding {
  padding-left: 1.5rem ;
  padding-right: 1.5rem ;
}
.tb-padding {
  padding-top: 1.5rem ;
  padding-bottom: 1.5rem ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
文本颜色
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.text-success {
  color: #04be02;
}
.text-primary {
  color: #4081d6;
}
.text-warning {
  color: #f0ad4e;
}
.text-info {
  color: #5bc0de;
}
.text-danger {
  color: #f95355;
}
.text-ellipsis {
  /*
    *截取文本用省略号追加在后面
    */
  overflow: hidden ;
  white-space: nowrap ;
  text-overflow: ellipsis ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
浮动
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.pull-left {
  display: block ;
  float: left ;
}
.pull-right {
  display: block ;
  float: right ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 任何一个容器，最右边如果需要一个可选择的标识，就是向右的箭头，就加这个class
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.selectable-flag {
  position: relative ;
}
.selectable-flag:after {
  border-style: solid ;
  border-color: #c9c9c9;
  border-width: 1px 1px 0 0 ;
  content: " ";
  display: block ;
  height: .6rem ;
  position: absolute ;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  right: .2rem ;
  top: .8rem ;
  width: .6rem ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 图片样式
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.img-responsive {
  display: block ;
  max-width: 100% ;
  height: auto ;
}
.img-circle {
  display: inline-block;
  border-radius: 100px;
}
.img-radius {
  display: inline-block;
  border-radius: 5px;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 定义VUE的transition样式，统一用slide-fade
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.slide-fade-enter-active,
.slide-fade-leave {
  transition: all 2s ease ;
}
.slide-fade-enter,
.slide-fade-leave-active {
  opacity: 0 ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 列表数据加载完毕提示字样
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.no-more {
  border-top: 1px solid #f9f9f9;
  color: #999;
  font-size: 1.1rem ;
  line-height: 25px ;
  padding: 5px ;
}
