.wk-notification {
  background-color: rgba(0, 0, 0, 0.85);
  left: 0 ;
  position: fixed ;
  top: 0 ;
  width: 100% ;
  z-index: 9999 ;
}
.wk-notification .notification-content {
  color: #fff;
  font-size: 1.2rem ;
  line-height: 2.5rem;
  padding: .3rem .3rem 0 .3rem ;
  text-align: center ;
}
.wk-notification .notification-handle-bar {
  background-color: rgba(120, 120, 120, 0.8);
  border-radius: 100px;
  display: block ;
  margin-left: auto ;
  margin-right: auto ;
  height: .35rem ;
  margin-bottom: .3rem ;
  width: 3rem ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 1. 项目名称：yfyk2h5
 2. 文件名称：src -> less -> components -> modal.less (modal框)
 3. 作者：zhaohuagang@lifang.com
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.wkzf-modal {
  background-color: #ffffff;
  box-shadow: 0 4px 23px 5px rgba(0, 0, 0, 0.2), 0 2px 6px rgba(0, 0, 0, 0.15);
  border-radius: 3px;
  display: none ;
  min-width: 17rem ;
  position: fixed;
  max-width: 60% ;
  z-index: 9998 ;
}
.wkzf-modal .modal-header {
  font-size: 1.6rem ;
  line-height: 3rem ;
  padding: 1.5rem 0 .75rem ;
  text-align: center ;
}
.wkzf-modal .modal-body {
  color: #999;
  font-size: 1.4rem ;
  line-height: 150% ;
  padding: 0 .75rem ;
  text-align: center ;
}
.wkzf-modal .modal-footer {
  border-top: 1px solid #E4E4E4;
  margin-top: 1.5rem ;
  text-align: center ;
}
.wkzf-modal .modal-footer .wkzf-btn {
  border-left: 1px solid #E4E4E4;
}
.wkzf-modal .modal-footer .wkzf-btn:nth-child(1) {
  border-left: none ;
}
.wkzf-modal-mask {
  background-color: rgba(0, 0, 0, 0.65);
  display: none ;
  height: 100% ;
  left: 0 ;
  position: fixed ;
  top: 0 ;
  width: 100% ;
  z-index: 9997 ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 1. 项目名称：yfyk2h5
 2. 文件名称：src -> less -> components ->  tab.less (标签页)
 3. 作者：zhaohuagang@lifang.com
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.wk-tabs .tabs-handle {
  background-color: #fff ;
  border-bottom: 1px solid #ddd;
  padding: 0 1rem;
}
.wk-tabs .tabs-handle:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.wk-tabs .tabs-handle > li {
  float: left ;
}
.wk-tabs .tabs-handle > li a {
  border-bottom: 2px solid transparent ;
  color: #4e4e4e;
  display: inline-block;
  font-size: 1.6rem ;
  line-height: 45px ;
  text-align: center ;
}
.wk-tabs .tabs-handle > li a.on {
  border-bottom-color: #4081d6;
  color: #4081d6;
}
.wk-tabs .tabs-frame {
  /*display : none ;*/
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 1. 项目名称：yfyk2h5
 2. 文件名称：src -> less -> components -> select.less (下拉选择器)
 3. 作者：zhaohuagang@lifang.com
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.wk-select {
  border-top: 1px solid #ddd;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.176);
  bottom: -19rem;
  display: none ;
  left: 0 ;
  position: fixed ;
  width: 100% ;
}
.wk-select .select-toolbar {
  background-color: #f6f6f6;
  border-bottom: 1px solid #E4E4E4;
  line-height: 4rem ;
  text-align: center ;
}
.wk-select .select-toolbar:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.wk-select .select-toolbar h5 {
  display: inline-block;
  font-size: 1.4rem ;
  font-weight: normal ;
  text-shadow: 0 -1px 0 #fff;
}
.wk-select .select-toolbar a {
  display: block ;
  font-size: 1.4rem ;
  line-height: 4rem ;
  text-shadow: 0 -1px 0 #fff;
}
.wk-select .select-toolbar .confirm {
  color: #04be02;
  float: right ;
  margin-right: 1rem ;
}
.wk-select .select-toolbar .cancel {
  color: #999;
  float: left ;
  margin-left: 1rem ;
}
.wk-select .select-items {
  background-color: rgba(255, 255, 255, 0.9);
  max-height: 15rem ;
  overflow: auto ;
}
.wk-select .select-items a {
  border-top: 1px solid #E4E4E4;
  color: #4e4e4e;
  display: block ;
  font-size: 1.4rem ;
  line-height: 3.5rem ;
  margin: 0 1rem ;
}
.wk-select .select-items a:nth-child(1) {
  border-top-color: transparent ;
}
.wk-select .select-items a .iconfont {
  color: #04be02;
  float: right ;
  font-size: 1.8rem ;
  margin-right: .5rem ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 1. 项目名称：yfyk2h5
 2. 文件名称：src -> less -> components -> tips.less (提示)
 3. 作者：zhaohuagang@lifang.com
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.wkzf-tips {
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 100px;
  color: #fff ;
  display: none ;
  font-size: 1.2rem ;
  line-height: 2rem ;
  padding: .5rem 1rem ;
  position: fixed ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
 1. 项目名称：yfyk2h5
 2. 文件名称：src -> less -> components -> form.less (按钮)
 3. 作者：zhaohuagang@lifang.com
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.wk-form .control-label {
  display: block ;
  font-size: 1.4rem ;
  line-height: 2.2rem;
  text-align: left ;
}
.wk-form .form-control {
  display: block ;
  font-size: 1.4rem ;
  line-height: 2.2rem;
  background-color: transparent ;
  border: 1px solid transparent ;
  outline: none ;
  width: 100% ;
}
.wk-form select.form-control {
  height: 2.2rem;
}
.form-horizontal .form-group {
  border-bottom: 1px solid #f9f9f9;
}
.form-horizontal .form-group:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.form-horizontal .form-group .control-label,
.form-horizontal .form-group .control-box {
  float: left ;
  margin: .6rem 0 ;
}
/*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
图片上传器部分风格样式设置
 -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
.wk-uploader {
  padding: .8rem 0 ;
}
.wk-uploader .uploader-files {
  display: inline-block;
}
.wk-uploader .uploader-files li {
  background-repeat: no-repeat;
  background-size: 100% 100% ;
  display: inline-block;
  height: 7rem ;
  margin: 0 1rem 1rem 0 ;
  position: relative ;
  width: 7rem ;
}
.wk-uploader .uploader-files li .selected-handle {
  background-color: #04be02;
  border-radius: 100px;
  display: none ;
  height: 2rem ;
  left: .2rem ;
  line-height: 2rem ;
  position: absolute ;
  text-align: center ;
  top: .2rem ;
  width: 2rem ;
}
.wk-uploader .uploader-files li .selected-handle i {
  color: #fff ;
  display: inline-block;
}
.wk-uploader .uploader-files li.on .selected-handle {
  display: inline ;
}
.wk-uploader .dashed-box {
  border: 2px dashed #ddd;
  border-radius: 0.3rem;
  display: inline-block;
  height: 7rem ;
  line-height: 7rem ;
  margin: 0 1rem 1rem 0 ;
  width: 7rem ;
}
.wk-uploader .uploader-handle {
  position: relative ;
}
.wk-uploader .uploader-handle:before,
.wk-uploader .uploader-handle:after {
  background: #E4E4E4;
  content: " ";
  display: block ;
  height: 2px ;
  left: 25% ;
  position: absolute ;
  top: 50% ;
  width: 50% ;
}
.wk-uploader .uploader-handle:after {
  transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
}
.wk-uploader .uploader-handle input[type='file'] {
  height: 100% ;
  left: 0 ;
  opacity: 0 ;
  position: absolute ;
  top: 0 ;
  width: 100% ;
}
.wk-uploader .remove-handle {
  display: none ;
  text-align: center ;
}
.wk-uploader .remove-handle i {
  color: #ddd;
  font-size: 3.6rem ;
}
