}
.wk-panel .panel-header {
  border-bottom: 1px solid #E4E4E4;
  font-size: 1.6rem ;
  font-weight: normal ;
  line-height: 22px ;
  margin-left: 1.5rem ;
  padding-top: 1rem;
  padding-bottom: 1rem;
}
.wk-panel .panel-body {
  /*-----------------------------------------------------------------------------------------------------------------------------------------------------------------------
        带缩略图的选项
        -----------------------------------------------------------------------------------------------------------------------------------------------------------------------*/
}
.wk-panel .panel-body .panel-item {
  border-bottom: 1px solid #f9f9f9;
