.business-card[data-v-532079ce] {
  background: url("../../assets/businessCard-bg.png");
  background-size: 100% 100%;
  display: block;
  padding: 1.5rem 1.5rem 1.1rem 1.5rem;
  border-bottom: 1px solid #E4E4E4;
  box-shadow: 0 -2px 6px 0 rgba(0, 0, 0, 0.1);
  color: #7c7c7c;
}
.business-card .photo[data-v-532079ce] {
  width: 5rem;
  height: 5rem;
  overflow: hidden;
  float: left;
}
.business-card .photo img[data-v-532079ce] {
  width: 100%;
}
.business-card .detail[data-v-532079ce] {
  margin-left: 6.5rem;
  padding-top: 0.3rem;
}
.business-card .detail .name[data-v-532079ce] {
  font-size: 1.6rem;
  color: black;
  line-height: 1;
  margin-bottom: 1rem;
}
.business-card .detail .icon-good[data-v-532079ce] {
  color: #CE8000;
  font-size: 1.1rem;
  line-height: 1.45;
  background-color: #FBE69A;
  border-radius: 4px;
  width: 1.6rem;
  padding: .1rem .3rem;
}
.business-card .detail .company-name[data-v-532079ce] {
  font-size: 1.4rem;
  margin-bottom: 0.75rem;
}
.business-card .detail .more-house[data-v-532079ce] {
  color: #4081D6;
  float: right;
  font-size: 1.4rem;
  line-height: 1.6rem;
}
.business-card .detail .more-house .iconfont[data-v-532079ce] {
  font-size: 1.4rem;
}
.business-card .detail .platform[data-v-532079ce] {
  font-size: 1.6rem;
}



