.xf-sources a[data-v-da603ade] {
  color: #4e4e4e;
  display: block ;
}
.xf-sources dl[data-v-da603ade] {
  border-bottom: 1px dotted #E4E4E4;
  margin-left: 1rem ;
  padding: 1rem 1rem 1rem 0 ;
}
.xf-sources dl[data-v-da603ade]:after {
  clear: both ;
  content: " ";
  display: block ;
  font-size: 0 ;
  height: 0 ;
  visibility: hidden ;
}
.xf-sources dl[data-v-da603ade]:last-child {
  border: none ;
}
.xf-sources dl dt[data-v-da603ade] {
  float: left ;
  height: 87px ;
  overflow: hidden ;
  width: 131px ;
}
.xf-sources dl dt img[data-v-da603ade] {
  height: 185px ;
  width: 100% ;
}
.xf-sources dl dd[data-v-da603ade] {
  margin-left: 141px ;
}
.xf-sources dl dd.title[data-v-da603ade] {
  font-size: 1.6rem ;
  /*
    *截取文本用省略号追加在后面
    */
  overflow: hidden ;
  white-space: nowrap ;
  text-overflow: ellipsis ;
}
.xf-sources dl dd.summary[data-v-da603ade] {
  color: #999;
  font-size: 1.1rem ;
  line-height: 22px ;
}
.xf-sources dl dd.tags span[data-v-da603ade] {
  background-color: #f9f9f9;
  color: #999;
  border-radius: 3px;
  display: inline-block;
  margin: 0 2px 2px 0 ;
  padding: 2px 5px ;
}
.xf-sources dl dd.price[data-v-da603ade] {
  color: #f95355;
  font-size: 1.6rem ;
  font-weight: 600 ;
}



